.welcome-page-container{
    height: 100vh;
    width: 100%;
    background-color: #fed7da;
    font-family: Montserrat;
}

.welcome-page-title{
    font-weight: bold;
    font-size: 50px;
    margin-top: 20vh;
    margin-left: 12vw;
}

.welcome-page-hand{
    vertical-align: middle;
    padding-bottom: 3vh;
    height: 70px;
    width: 70px;
}

.welcome-subtitle{
    font-size: 30px;
    margin-left: 12vw;
}

.welcome-page-laptop{
    vertical-align: middle;
    height: 80px;
    width: 80px;
    padding-bottom: 2vh;
}

.welcome-page-photo{
    width: 350px;
    height: 350px;
    margin-left: 65vw;
    margin-bottom: -70vh;
}

.welcome-page-photo-mobile{
    display: none;
}

@media screen and (min-width: 320px) and (max-width: 414px) and (orientation: portrait) {

    .welcome-page-photo{
        display: none;
    }

    .welcome-page-photo-mobile{
        display: flex;
        height: 40vh;
        margin-bottom: 15%;
        margin-left: 0vw;
    }

    .welcome-page-container{
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
    }
    
    .welcome-page-title{
        font-weight: bold;
        font-size: 25px;
        margin-left: 0vw;
        margin-top: 5vh;
    }

    .welcome-subtitle{
        font-size: 20px;
        margin-left: 0vw;
    }

    .welcome-page-hand {
        padding-bottom: 0vh;
    }

}